import React from 'react'
import {
  Table,
  Icon,
  Input,
  Button,
  Popconfirm,
  Tooltip,
  Dropdown,
  Menu
} from 'antd'
import { Helmet } from 'react-helmet'
import { Link } from 'react-router-dom'
import { getFormattedDate, getFormattedTime } from 'utils'
import table from './data.json'
import styles from './style.module.scss'

const MenuItem = Menu.Item

const menu = (
  <Menu>
    <MenuItem key='0'>
      <Link className='ant-dropdown-link' to='/'>
        View vendor
      </Link>
    </MenuItem>
    <MenuItem key='1'>
      <Link className='ant-dropdown-link' to='/'>
        View vendor users
      </Link>
    </MenuItem>
    <Menu.Divider />
    <MenuItem key='2'>
      <Link className='ant-dropdown-link' to='/'>
        Edit
      </Link>
    </MenuItem>
    <MenuItem key='3'>
      <Link className='ant-dropdown-link' to='/'>
        Delete
      </Link>
    </MenuItem>
  </Menu>
)

class VendorsList extends React.Component {
  state = {
    tableData: table.data,
    statusRowId: '',
    // data: table.data,
    // filterDropdownVisible: false,
    // searchText: '',
    // filtered: false,
    selectedRowKeys: []
  }

  handleStatus = ({ key }) => {
    const { tableData, statusRowId } = this.state
    console.log(tableData, statusRowId)

    this.setState({
      tableData: tableData.map(el => {
        return el.vendor_id === statusRowId ? { ...el, status: key } : el
      })
    })
    // console.log(tableData[key])
  }

  getColumnSearchProps = dataIndex => ({
    filterDropdown: ({
      setSelectedKeys,
      selectedKeys,
      confirm,
      // statusRowId,
      clearFilters
    }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={node => {
            this.searchInput = node
          }}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={e =>
            setSelectedKeys(e.target.value ? [e.target.value] : [])
          }
          onPressEnter={() => this.handleSearch(selectedKeys, confirm)}
          style={{ width: 188, marginBottom: 8, display: 'block' }}
        />
        <Button
          type='primary'
          onClick={() => this.handleSearch(selectedKeys, confirm)}
          icon='search'
          size='small'
          style={{ width: 90, marginRight: 8 }}
        >
          Search
        </Button>
        <Button
          onClick={() => this.handleReset(clearFilters)}
          size='small'
          style={{ width: 90 }}
        >
          Reset
        </Button>
      </div>
    ),
    filterIcon: filtered => (
      <Icon type='search' style={{ color: filtered ? '#1890ff' : undefined }} />
    ),
    onFilter: (value, record) =>
      record[dataIndex]
        .toString()
        .toLowerCase()
        .includes(value.toLowerCase()),
    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => this.searchInput.select())
      }
    },
    render: text => {
      // const { searchText } = this.statereturn
      // console.log(searchText)
      return <span>{text}</span>
    }
  })

  handleSearch = (selectedKeys, confirm) => {
    confirm()
    // this.setState({ searchText: selectedKeys[0] })
  }

  handleReset = clearFilters => {
    clearFilters()
    // this.setState({ searchText: '' })
  }

  handleChange = (pagination, filters, sorters) => {
    console.log('Various parameters', pagination, filters, sorters)
  }

  onSelectChange = selectedRowKeys => {
    console.log('selectedRowKeys changed: ', selectedRowKeys)
    this.setState({ selectedRowKeys })
  }

  activateSelected = () => {
    // const { selectedRowKeys, tableData } = this.state
    // selectedRowKeys.some(function (v) {
    //     return tableData.indexOf(v) >= 0;
    // });
    // tableData.map((item, index) => index == sele)
  }

  disableSeleted = () => {

  }

  render () {
    const { tableData, selectedRowKeys } = this.state
    console.log(selectedRowKeys)
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange
    }

    const statusMenu = (
      <Menu onClick={this.handleStatus}>
        <MenuItem className='ant-dropdown-link' key='Active'>
          Active
        </MenuItem>
        <MenuItem className='ant-dropdown-link' key='Pending'>
          Pending
        </MenuItem>
        <MenuItem className='ant-dropdown-link' key='Disabled'>
          Disabled
        </MenuItem>
      </Menu>
    )

    const columns = [
      // ID
      // Name
      // email
      // Registered on
      // Actions
      // Status
      {
        title: 'ID',
        dataIndex: 'vendor_id',
        key: 'vendor_id',
        render: (text, record) => (
          <Link
            className='utils__link--underlined'
            to={`/vendors/vendor/${record.vendor_id}`}
          >
            {`#${text}`}
          </Link>
        ),
        sorter: (a, b) => a.vendor_id - b.vendor_id,
        ...this.getColumnSearchProps('vendor_id')
      },
      {
        title: 'Name',
        dataIndex: 'name',
        key: 'name',
        sorter: (a, b) => a.name.localeCompare(b.name),
        ...this.getColumnSearchProps('name'),
        render: (text, record) => (
          <Link
            className='utils__link--underlined'
            to={`/vendors/vendor/${record.vendor_id}`}
          >
            {text}
          </Link>
        )
      },
      {
        title: 'Email',
        dataIndex: 'email',
        key: 'email',
        ...this.getColumnSearchProps('email')
      },
      {
        title: 'Registration',
        dataIndex: 'createdAt',
        key: 'createdAt',
        sorter: (a, b) => {
          return (
            new Date(a.createdAt).getTime() - new Date(b.createdAt).getTime()
          )
        },
        render: text => {
          return `${getFormattedDate(text)} ${getFormattedTime(text)}`
        }
      },

      {
        title: '',
        key: 'action',
        width: '100px',
        render: () => (
          <Dropdown overlay={menu} trigger={['click']}>
            <a className='ant-dropdown-link' href='#'>
              <Icon type='setting' theme='filled' />
              <Icon type='down' />
            </a>
          </Dropdown>
          //   (
          //     <span>
          //       <Button icon='edit' className='mr-1' size='small' />
          //       {tableData.length >= 1 ? (
          //         <Popconfirm
          //           title='Sure to delete?'
          //           onConfirm={() => this.handleDelete(record.key)}
          //         >
          //           <Button icon='close' size='small' />
          //         </Popconfirm>
          //       ) : null}
          //     </span>
          //   )
        )
      },
      {
        title: 'Status',
        dataIndex: 'status',
        key: 'status',
        width: '10%',
        render: (text, record) => {
          let badge = ''
          if (text === 'Active') badge = 'badge-success'
          else if (text === 'New') badge = 'badge-info'
          else if (text === 'Pending') badge = 'badge-danger'
          else if (text === 'Disabled') badge = 'badge-seconadary'
          return (
            <Dropdown overlay={statusMenu} trigger={['click']}>
              <span className={`font-size-12 badge ${badge}`}>
                {text}

                <Icon
                  onClick={() =>
                    this.setState({ statusRowId: record.vendor_id })
                  }
                  type='down'
                />
              </span>
              {/* <a className='ant-dropdown-link' href='#'>
              {text}
              <Icon type='down' />
            </a> */}
            </Dropdown>
          )
        },
        sorter: (a, b) => {
          if (a.status === b.status) return 0
          if (a.status) return -1
          return 1
        },
        filters: [
          {
            text: 'New',
            value: 'New'
          },
          {
            text: 'Active',
            value: 'Active'
          },
          {
            text: 'Pending',
            value: 'Pending'
          },
          {
            text: 'Disabled',
            value: 'Disabled'
          }
        ],
        filterMultiple: true,
        onFilter: (value, record) => record.status.indexOf(value) === 0
        // render: text => (
        //   <span
        //     className={`font-size-12 badge ${
        //       text ? 'badge-success' : 'badge-danger'
        //     }`}
        //   >
        //     {text ? 'Active' : 'Inactive'}
        //   </span>
        // )
      }
    ]

    return (
      <div>
        <Helmet title='Vendors List' />
        <div className='card'>
          <div className='card-header'>
            <div className='utils__title'>
              <strong>Vendors List</strong>
            </div>
            <div className={`pull-right ${styles.rightItems}`}>
              <Tooltip placement='topLeft' title='Add new vendor'>
                <Button type='link'>
                  <Icon
                    type='plus-circle'
                    theme='filled'
                    style={{ fontSize: '30px' }}
                  />
                </Button>
              </Tooltip>
              {selectedRowKeys.length > 0 && (
                <>
                  <Tooltip
                    placement='bottomRight'
                    title={
                      selectedRowKeys.length === 1
                        ? 'Delete vendor'
                        : 'Delete vendors'
                    }
                  >
                    <Popconfirm
                      title={`Delete ${selectedRowKeys.length} ${
                        selectedRowKeys.length === 1 ? 'vendor?' : 'vendors?'
                      }`}
                      onConfirm={() => this.handleDelete()}
                    >
                      <Button type='link'>
                        <Icon
                          theme='filled'
                          type='delete'
                          style={{ fontSize: '30px' }}
                        />
                      </Button>
                    </Popconfirm>
                  </Tooltip>
                  <Popconfirm
                    title='Activate selected vendors'
                    onConfirm={() => this.activateSelected()}
                  >
                    <button type='button' className='btn btn-success'>
                      Activate
                    </button>
                  </Popconfirm>
                  <Popconfirm
                    title='Disable selected vendors'
                    onConfirm={() => this.disableSeleted()}
                  >
                    <button type='button' className='btn btn-danger'>
                      Disable
                    </button>
                  </Popconfirm>
                </>
              )}
            </div>
          </div>
          <div className='card-body'>
            <Table
              className='utils__scrollTable tableCustom'
              scroll={{ x: '100%' }}
              rowSelection={rowSelection}
              columns={columns}
              dataSource={tableData}
              onChange={this.handleChange}
              rowKey='vendor_id'
            />
          </div>
        </div>
      </div>
    )
  }
}

export default VendorsList
